<template>
  <div>
    <router-view></router-view>
    <ul class="travel-bottom">
      <li v-for="(item,i) in data"  @click="fn(item.url)">
        <router-link :to="item.url" :class="{active:item.url==url}">
          <As :text="item.text" class="bars">
            <img :src="item.pic1" slot="noselect" v-show="item.url!==url" alt="">
            <img :src="item.pic2" slot="select" v-show="item.url==url" alt="">
          </As>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import As from '../components/item.vue'
  export default {
    beforeCreate:function () {
      this.$router.push('/travel/travelHome')
    },
    components:{
      As
    },
    data:function () {
      return {
        data:[
          {
            url:'/travel',
            text:'旅游首页',
            pic1:require('../assets/images/icon/luggage_02.png'),
            pic2:require('../assets/images/icon/luggage_01.png')
          },{
            url:'/aim',
            text:'目的地',
            pic1:require('../assets/images/icon/footer_0322_06.png'),
            pic2:require('../assets/images/icon/footer_0322_02.png')
          },{
            url:'/history',
            text:'浏览历史',
            pic1:require('../assets/images/icon/footer_0322_07.png'),
            pic2:require('../assets/images/icon/footer_0322_03.png')
          },{
            url:'/mine',
            text:'个人',
            pic1:require('../assets/images/icon/footer_0322_08.png'),
            pic2:require('../assets/images/icon/footer_0322_04.png')
          }
        ],
        url:''
      }
    },
    methods:{
      fn:function(index){
        this.url=index;
        if(this.url==='/travel'){
          window.location.hash='#/travel/travelHome'
        }
      }
    },
    created:function(){
      this.url=window.location.hash.substr(1,7);
//      console.log(window.location.hash)

    }
  }
</script>

<style>
  .travel-bottom{
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: fixed;
    left: 0;
    bottom: 0;
    padding:0.5rem 0;
    background-color: #fff;
    box-sizing: border-box;
    z-index: 999;
  }
  .travel-bottom>li{
    width: 25%;
    text-align: center;
    font-size: 0.8rem;
  }
  .travel-bottom a{
    color: #666;
    text-decoration: none;
  }
  .bars>img{
    width: 1.8rem;
    height: 1.8rem;
  }
  .travel-bottom>li>.active{
    color: blue;
  }
</style>



