<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <slot name="banners"></slot>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
  import '../assets/libs/Swiper-3.4.2/dist/js/swiper.min.js'
  export default {
    mounted:function () {
      new Swiper('.swiper-container',{
        loop:true,
        autoplay:2000,
        direction:'horizontal',
        pagination : '.swiper-pagination'
      })
    }
  }
</script>

<style>
  @import "../assets/libs/Swiper-3.4.2/dist/css/swiper.min.css";
  .swiper-pagination>span{
    width: 0.5rem;
    height: 0.5rem;
  }
</style>



