import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

import Home from '../pages/home.vue'
import Aim from '../pages/aim.vue'
import History from '../pages/history.vue'
import Mine from '../pages/mine.vue'

import Travel from '../pages/travel.vue'

import Remen from '../components/aim/remen.vue'
import Guonei from '../components/aim/guonei.vue'
import Guowai from '../components/aim/guowai.vue'

import TravelHome from '../components/travel/travelHome.vue'
import TravelList from '../components/travel/trvalelList.vue'
import TravelDetale from '../components/travel/travelDetale.vue'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },{
      path: '/aim',
      name: 'Aim',
      component: Aim,
      children:[
        {path:'remen',name:'Remen',component:Remen},
        {path:'guonei',name:'Guonei',component:Guonei},
        {path:'guowai',name:'Guowai',component:Guowai}
      ]
    },{
      path: '/history',
      name: 'History',
      component: History
    },{
      path: '/mine',
      name: 'Mine',
      component: Mine
    },{
      path: '/travel',
      name: 'Travel',
      component: Travel,
      children:[
        {path:'/',name:'TravelHome',component:TravelHome},
        {path:'travelHome',name:'TravelHome',component:TravelHome},
        {path:'travelList',name:'TravelList',component:TravelList},
        {path:'travelDetale',name:'TravelDetale',component:TravelDetale}
      ]
    }
  ]
})



