<template>
  <div class="aim">
    <div class="aim-topbar">
      <p class="aim-tittle"><span></span><span>选择目的地</span><span></span></p>
      <div class="aim-search">
        <div class="aim-search-img">
          <img src="../assets/images/icon/galss_03.png" alt="">
        </div>
        <input type="text" value="搜索旅行地/酒店/景点">
      </div>
    </div>
    <div class="aim-top-model"></div>
    <div class="aim-main">
      <div class="aim-main-bar">
        <ul class="aim-bar">
          <li v-for="item in data"  @click="fn(item.url)" :class="{border:item.url==url}">
            <router-link :to="item.url"  :class="{active:item.url==url}">
              <Item :text="item.text">
                <div slot="noselect" :class="[{'aim-bar-icon':true},item.url==url?item.istrue:item.isfalse]"></div>
              </Item>
            </router-link>
          </li>
        </ul>
      </div>
      <div class="aim-router">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  import Item from '../components/item.vue'
  export default {
    components:{
      Item
    },
    data:function () {
      return {
        data:[
          {
            url:'/aim/remen',
            text:'热门',
            isfalse:'aim-remen-false',
            istrue:'aim-remen-true'
          },{
            url:'/aim/guonei',
            text:'国内',
            isfalse:'aim-guonei-false',
            istrue:'aim-guonei-true'
          },{
            url:'/aim/guowai',
            text:'国外',
            isfalse:'aim-guowai-false',
            istrue:'aim-guowai-true'
          },
        ],
        url:''
      }
    },
    methods:{
      fn:function (url) {
        this.url=url;
//        console.log(this.url)
      }
    },
    beforeCreate:function () {
      this.$router.push('/aim/remen')
    },
    created:function () {
      this.url=window.location.hash.substring(1,window.location.hash.length);
    },
//    props:['aim'],
//    computed:{
//      flag:function(){
//        if(this.url==='/aim'){
//          return true
//        }
//        return false
//      }
//    }
  }

</script>

<style>
  .aim-topbar{
    width: 100%;
    background-color: blue;
    color: #ffffff;
    padding:0.4rem 0.8rem;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    top: 0;
  }
  .aim-top-model{
    width: 100%;
    height: 5.75rem;
  }
  .aim-tittle{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .aim-tittle>span:nth-of-type(2n+1){
    display: inline-block;
    width: 2rem;
    height: 2rem;
  }
  .aim-tittle>span:nth-of-type(1){
    background: url("../assets/images/icon/sprites.png") no-repeat -100px -30px;
    transform: scale(0.8);
  }
  .aim-search{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border:1px solid #ffffff;
    border-radius: 50px;
    padding:0.1rem 0.5rem;
    box-sizing: border-box;
    margin-top:1rem;
  }
  .aim-search>input{
    outline: none;
    background-color: blue;
    width: 85%;
    height: 1.5rem;
    border:none;
    font-size: 0.9rem;
    color: #ccc;
  }
  .aim-search-img>img{
    vertical-align: middle;
    transform: scale(0.7);
  }

  .aim-main{
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    padding:0 0.5rem 0 0;
  }
  .aim-main-bar{
    width: 20%;
    height: 26.7rem;
    background-color: #ccc;
  }
  .aim-bar{
    width: 100%;
  }
  .aim-bar>li{
    width: 100%;
    height: 4.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding:0.3rem 0;
  }
  .aim-bar>.border{
    background-color: #fff;
    border-left:0.3rem solid #ffd617;
  }
  /*background: url("../assets/images/icon/sprites.png") no-repeat -450px -175px;点击后的图片位置*/
  .aim-bar-icon{
    width: 3rem;
    height: 3rem;
    transform: scale(0.7);
  }
  .aim-remen-false{
    background: url("../assets/images/icon/sprites.png") no-repeat -450px -100px;
  }
  .aim-remen-true{
    background: url("../assets/images/icon/sprites.png") no-repeat -450px -179px;
  }
  .aim-guonei-false{
    background: url("../assets/images/icon/sprites.png") no-repeat -535px -100px;
  }
  .aim-guonei-true{
    background: url("../assets/images/icon/sprites.png") no-repeat -535px -179px;
  }
  .aim-guowai-false{
    background: url("../assets/images/icon/sprites.png") no-repeat -620px -100px;
  }
  .aim-guowai-true{
    background: url("../assets/images/icon/sprites.png") no-repeat -620px -179px;
  }
  .aim-bar>li>a{
    text-align: center;
    text-decoration: none;
    color: #777;
    font-size: 1rem;
  }
  .aim-bar>li>.active{
    color: blue;
  }

  .aim-router{
    width: 78%;
  }

</style>



