<template>
  <div id="app">
    <router-view/>
    <div class="diban"></div>
    <ul class="main-bar">
      <li v-for="(item,i) in data"  @click="fn(item.url)">
        <router-link :to="item.url" :class="{active:item.url==url}">
          <Item :text="item.text" class="bars">
            <img :src="item.pic1" slot="noselect" v-show="item.url!==url" alt="">
            <img :src="item.pic2" slot="select" v-show="item.url==url" alt="">
          </Item>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
  import Item from './components/item.vue'
  import Aim from './pages/aim.vue'
  export default {
    components:{
      Item,Aim
    },
    data:function () {
      return {
        data:[
          {
            url:'/home',
            text:'首页',
            pic1:require('./assets/images/icon/footer_0322_05.png'),
            pic2:require('./assets/images/icon/footer_0322_01.png')
          },{
            url:'/aim',
            text:'目的地',
            pic1:require('./assets/images/icon/footer_0322_06.png'),
            pic2:require('./assets/images/icon/footer_0322_02.png')
          },{
            url:'/history',
            text:'浏览历史',
            pic1:require('./assets/images/icon/footer_0322_07.png'),
            pic2:require('./assets/images/icon/footer_0322_03.png')
          },{
            url:'/mine',
            text:'个人',
            pic1:require('./assets/images/icon/footer_0322_08.png'),
            pic2:require('./assets/images/icon/footer_0322_04.png')
          }
        ],
        url:'/home'
      }
    },
    beforeCreate:function(){
      this.$router.push(window.location.hash.substring(1,window.location.hash.length));
//      console.log(window.location.hash.substring(1,window.location.hash.length))
    },
    created:function(){
      this.url=window.location.hash.substring(1,window.location.hash.length);
      var a=window.location.hash.substr(1,4);
      if(a==='/aim'){
        this.url=window.location.hash.substr(1,4);
      }else if(a==='/hom' || a==='/min'){
        this.url=window.location.hash.substr(1,5);
      }else if(a==='/tra'){
        this.url=window.location.hash.substr(1,6);
      }else{
        this.url=window.location.hash.substr(1,8);
      }
    },
    methods:{
      fn:function(index){
        this.url=index;
//        点击目标时保证目标页不会出现空白
        if(this.url==='/aim'){
          window.location.hash='#/aim/remen'
        }

//        console.log(window.location.hash.substring(1,window.location.hash.length))
      },
    },
    computed:{
//      flag:function () {
//        if(this.url.substr(1,4)==this.data.url.substr(1,4)){
//          return true
//        }
//        return false
//      }
    }
  }
</script>

<style>
  .main-bar{
    width: 100%;
    display: flex;
    justify-content: space-around;
    position: fixed;
    left: 0;
    bottom: 0;
    padding:0.5rem 0;
    background-color: #fff;
    box-sizing: border-box;
  }
  .main-bar>li{
    width: 25%;
    text-align: center;
    font-size: 0.8rem;
  }
  .main-bar a{
    color: #666;
    text-decoration: none;
  }
  .bars>img{
    width: 1.8rem;
    height: 1.8rem;
  }
  .main-bar>li>.active{
    color: blue;
  }
  .diban{
    width: 100%;
    height: 4rem;
  }
</style>



